import React, { useState } from "react";
import "./Style/ThisDayStyle.css";
import axios from "axios";

function ThisDayData() {
  const [cityInitalData, setCityData] = useState({
    currentCity: "City",
    currentCityTime: "Today's Date & Time",
    currentTemperture: " C°",
    currentTempertureIcon: "",
    weatherDescription: "Description",
    weatherFeelsLike: " C°",
    windSpeed: "km/h",
    windDegree: "Wind Degree",
    Humidity: "%",
    Precipitation: "%",
  });

  const fetchCityApi = {
    url: "",
    method: "POST",
  };

  return (
    <div id="thisday-maindiv">
      <div id="thisday-data-container">
        <div id="today-plus-city" className="thisday-data-container-children">
          <div id="city-and-date">
            <div id="city">{cityInitalData.currentCity}</div>

            <div id="thisday-date">{cityInitalData.currentCityTime}</div>
          </div>

          <div id="logo-and-degrees">
            <p id="main-degrees">{cityInitalData.currentTemperture}</p>
            <img src={cityInitalData.currentTempertureIcon} />
          </div>
        </div>

        <div
          id="input-plus-extraData"
          className="thisday-data-container-children"
        >
          <div id="input-container">
            <input id="input-id" placeholder="Type your city name here" />
            <p>Note: A 2 word city must have a whitespace between its words!</p>
            <button
              id="btn"
              onClick={() => {
                let newInputValue = document.getElementById("input-id").value;
                fetchCityApi.url =
                  "http://api.weatherstack.com/current?access_key=87fac650b19809315f8ce8bff4aca526&query=" +
                  newInputValue;
                axios(fetchCityApi)
                  .then((response) => {
                    setCityData({
                      currentCity: response.data.location.name,
                      currentCityTime: response.data.location.localtime,
                      currentTemperture:
                        response.data.current.temperature + "C °",
                      currentTempertureIcon:
                        response.data.current.weather_icons,
                      weatherDescription:
                        response.data.current.weather_descriptions,
                      weatherFeelsLike: response.data.current.feelslike + "C °",
                      windSpeed: response.data.current.wind_speed + "km/h",
                      windDegree: response.data.current.wind_degree ,
                      Humidity: response.data.current.humidity + "%",
                      Precipitation: response.data.current.precip + "%",
                    });
                  })
                  .catch((err) => {
                    console.log(err);
                  });
              }}
            >
              Get Data
            </button>
          </div>

          <div id="extra-data">
            <div id="sunrise-sunset">
              <p>
                Weather Description:<p>{cityInitalData.weatherDescription}</p>
              </p>
              <p>
                Feels Like: <p>{cityInitalData.weatherFeelsLike}</p>
              </p>
            </div>

            <div id="wind-rain">
              <p>
                Wind Speed:<p>{cityInitalData.windSpeed}</p>{" "}
              </p>
              <p>
                Wind Degree:<p>{cityInitalData.windDegree}</p>{" "}
              </p>
            </div>

            <div id="highest-lowest">
              <p>
                Humidity: <p>{cityInitalData.Humidity}</p>
              </p>
              <p>
                Precipitation: <p>{cityInitalData.Precipitation}</p>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ThisDayData;
